.sidebar{
    flex: 30%;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #dedddd;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebar-img{
    width: 90%;
    margin-top: 15px;
    height: 500px;
    object-fit:cover;
}

.sidebar-item{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebar-title{
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 12px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}

.sidebar-item > p{
    padding: 30px;
}

.sidebar-list{
    list-style: none;
    margin-bottom: 30px;
}

.sidebar-list-item{
    display: inline-block;
    width: 50%;
    margin-top: 10px;
    cursor: pointer;
}

.sidebar-social{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.sidebar-icon{
    font-size: 18px;
    margin-left: 15px;
    cursor: pointer;
}