.settings{
    display: flex;
}

.settings-wrapper{
    flex: 9;
    padding: 20px;
}



.settings-title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settings-updatetitle{
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
}

.settings-deletetitle{
    color: red;
    font-size: 12px;
    cursor: pointer;
}

.settings-form{
    display: flex;
    flex-direction: column;
}

.settings-profilepicture{
    display: flex;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.settings-form-img {
    width: 70px;
    height: 70px;
    border-radius: 20px;
    object-fit: cover;
}

.settings-profilepicture-icon{
    width: 25px;
    height: 25px;
    background-color: lightcoral;
    border-radius: 50%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
    cursor: pointer;
}

.settings-form > label{
    font-size: 20px;
    margin-top: 20px;
}

.settings-form > input{
    color: gray;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgray;
}

.settings-submit{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}